#cssmenu {
  &:after {
    content: ".";
    display: block;
    clear: both;
    visibility: hidden;
    line-height: 0;
    height: 0;
  }
  > ul {
    &:after {
      content: ".";
      display: block;
      clear: both;
      visibility: hidden;
      line-height: 0;
      height: 0;
    }
    > li {
      float: left;
      margin-right: 20px;
      > a {
        text-transform: uppercase;
        padding: 15px 0px;
        color: @black;
        .transition(color .2s ease);
      }
    }
    > li.has-sub {
      > a {
        padding-right: 15px;
        &:after {
          top: 21px;
          right: 0px;
          border-bottom: 1px solid #000000;
          border-right: 1px solid #000000;
          .transform(rotate(45deg));
        }
      }
    }
    > li.active {
      > a {
        color: @color_2;
      }
    }
  }
  ul {
    li {
      a {
        &:after {
          position: absolute;
          width: 4px;
          height: 4px;
          content: "";
          -webkit-transition: border-color 0.2s ease;
          -moz-transition: border-color 0.2s ease;
          -o-transition: border-color 0.2s ease;
          transition: border-color 0.2s ease;
        }
      }
      &:hover {
        >a {
          color: @color_2;
        }
        >ul {
          >li {
            height: 32px;
          }
        }
      }
    }
    ul {
      li.has-sub {
        >a {
          &:after {
            top: 25px;
            right: 10px;
            border-bottom: 1px solid #dddddd;
            border-right: 1px solid #dddddd;
            -webkit-transform: rotate(-45deg);
            -moz-transform: rotate(-45deg);
            -ms-transform: rotate(-45deg);
            -o-transform: rotate(-45deg);
            transform: rotate(-45deg);
          }
        }
      }
      position: absolute;
      left: -9999px;
      li {
        z-index: 100;
        height: 0;
        -webkit-transition: height .2s ease;
        -moz-transition: height .2s ease;
        -o-transition: height .2s ease;
        transition: height .2s ease;
        a {
          padding: 20px;
          width: 160px;
          background: #333333;
          text-decoration: none;
          color: @color_3;
          -webkit-transition: border-color 0.2s ease;
          -moz-transition: border-color 0.2s ease;
          -o-transition: border-color 0.2s ease;
          transition: border-color 0.2s ease;
          &:hover {
            color: @color_4;
          }
        }
        &:hover {
          >a {
            color: @color_4;
          }
        }
      }
      ul {
        margin-left: 100%;
        top: 0;
      }
    }
  }
  li {
    &:hover {
      >ul {
        left: auto;
      }
    }
  }
  #menu-button {
    display: none;
  }
}
#menu-line {
  position: absolute;
  top: 0;
