      display: inline-block;
      height: 1px;
      background: #23292c;
      position: absolute;
      top: 50%;
      z-index: -1;
      left: 0;
    }
    &:after {
      content: '';
      width: 250px;
      display: inline-block;
      height: 1px;
      background: #23292c;
      position: absolute;
      top: 50%;
      z-index: -1;
      right: 0;
    }
  }
  .item_inner {
    border-color: #ffffff;
    color: @color_4;
    padding: 0px;
    overflow: hidden;
    a {
      color: @color_4;
    }
    &:hover {
      .des_container {
        opacity: 0.9;
        visibility: visible;
        -webkit-transform: scale(1) translateX(0);
        -moz-transform: scale(1) translateX(0);
        -ms-transform: scale(1) translateX(0);
        transform: scale(1) translateX(0);
      }
    }
    .des_container {
      visibility: hidden;
      -webkit-transform: scale(0.9);
      -moz-transform: scale(0.9);
      -ms-transform: scale(0.9);
      transform: scale(0.9);
      -webkit-transition: all 0.6s ease;
      -moz-transition: all 0.6s ease;
      transition: all 0.6s ease;
      opacity: 0;
