      text-align: center;
      border: 1px solid #ffffff;
      outline: 15px solid #ec008c;
      background-color: #ec008c;
      position: absolute;
      top: 15px;
      left: 15px;
      right: 15px;
      bottom: 15px;
      z-index: 10;
      transition: all 0.5 ease-in;
