  }
}
.item_inner {
  border: 1px solid #adadad;
  padding: 14px;
  background: #ffffff;
  position: relative;
  a {
    display: block;
  }
  .img_container {
    position: relative;
    .icon-zoom {
      opacity: 0;
      filter: alpha(opacity=100);
      position: absolute;
      z-index: 1000;
      top: 50%;
      left: 50%;
      margin: -30px;
    }
    &:hover {
      .icon-zoom {
        opacity: 1;
      }
      .overlay {
        &:after {
          opacity: 1;
          filter: alpha(opacity=100);
        }
      }
    }
  }
  .overlay {
    &:after {
      background-color: rgba(37, 41, 44, 0.7);
      content: "";
      display: block;
      position: absolute;
      top: 0;
      bottom: 0;
      right: 0;
      left: 0;
      z-index: 100;
      transition: all 0.3s ease-out;
      -webkit-transition: all 0.3s ease-out;
      opacity: 0;
      filter: alpha(opacity=0);
    }
  }
  .btn {
    border-radius: 50px 0px 0px 50px;
    position: absolute;
    right: -1px;
    bottom: 14px;
    padding-left: 40px;
    span {
      padding-top: 10px;
    }
  }
  .des_container {
    min-height: 80px;
    padding: 14px 0px 40px;
    .info {
