        height: 2px;
        background: #000000;
      }
      &::before {
        content: '';
        position: absolute;
        top: 25px;
        right: 20px;
        display: block;
        width: 15px;
        height: 3px;
        border-top: 2px solid #000000;
        border-bottom: 2px solid #000000;
      }
    }
    .submenu-button {
      position: absolute;
      z-index: 10;
      right: 0;
      top: 0;
      display: block;
      border-left: 1px solid rgba(120, 120, 120, 0.15);
      height: 52px;
      width: 52px;
      cursor: pointer;
      &::after {
        content: '';
        position: absolute;
        top: 21px;
        left: 26px;
        display: block;
        width: 1px;
        height: 11px;
        background: #000000;
        z-index: 99;
      }
      &::before {
        content: '';
        position: absolute;
        left: 21px;
        top: 26px;
        display: block;
        width: 11px;
        height: 1px;
        background: #000000;
        z-index: 99;
      }
    }
    .submenu-button.submenu-opened {
      &:after {
        display: none;
      }
    }
  }
  #cssmenu.align-center {
    >ul {
      text-align: left;
      >li {
        float: none;
        display: block;
      }
    }
  }
  #cssmenu.align-right {
    ul {
      ul {
        text-align: left;
      }
    }
    >ul {
      >li {
        float: none;
        display: block;
      }
    }
  }
  #menu-line {
    display: none;
  }
}
